.breed-select {
  width: 200px;
  height: 30px;
  margin-top: 15px;
  margin-bottom: 15px;
}

.error.show {
  color: red;
  display: block;
}

.hide, .error {
  display: none;
}

.loader {
  color: #ff3d00;
  letter-spacing: 2px;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 28px;
  font-weight: bold;
  display: inline-block;
  position: relative;
}

.loader:after {
  content: "Loading";
  color: #fff;
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  animation: animloader 10s ease-in infinite;
  position: absolute;
  top: 0;
  left: 0;
  overflow: hidden;
}

@keyframes animloader {
  0% {
    width: 0%;
  }

  100% {
    width: 100%;
  }
}

